import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  ColumnsProps,
  DefaultSortProps,
  enumColumn,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { CLASS_NAME, ProcessoJudicial } from '../../../../models/dtos/mfp/processoJudicial';
import { INDICATIVO_ACAO_JUDICIAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoAcaoJudicialEnum';
import { INDICATIVO_MATERIA_PROCESSO_ENUM_VALUES } from '../../../../models/enumerated/mfp/indicativoMateriaProcessoEnum';
import { TIPO_PROCESSO_JUDICIAL_ENUM_VALUES } from '../../../../models/enumerated/mfp/tipoProcessoJudicialEnum';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'numeroProcesso',
    order: OrderType.ASC,
  },
];

const ProcessoJudicialSearchView: FC<SearchViewProps<ProcessoJudicial>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    enumColumn(
      {
        dataField: 'tipoProcesso',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Tipo Processo',
      },
      TIPO_PROCESSO_JUDICIAL_ENUM_VALUES
    ),
    {
      dataField: 'numeroProcesso',
      editable: true,
      sort: true,
      text: 'Nº Processo',
    },
    enumColumn(
      {
        dataField: 'indicativoAcaoJudicial',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Indicativo Ação Judicial',
      },
      INDICATIVO_ACAO_JUDICIAL_ENUM_VALUES
    ),
    enumColumn(
      {
        dataField: 'indicativoMateriaProcesso',
        editable: false,
        filter: {
          type: FilterType.SELECT,
        },
        sort: true,
        text: 'Indicativo Matéria Processo',
      },
      INDICATIVO_MATERIA_PROCESSO_ENUM_VALUES
    ),
  ];

  return (
    <WindowComponent
      title="Manutenção de Processos Administrativos/Judiciais"
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        columns={columns}
        defaultSorted={defaultSorted}
        dtoClassName={CLASS_NAME}
        extraData={getExtraData(props, 'ProcessoJudicialEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ProcessoJudicialSearchView;
